@charset "UTF-8";
@import url("https://cdnjs.cloudflare.com/ajax/libs/slick-carousel/1.6.0/slick.min.css");
@import url("https://cdnjs.cloudflare.com/ajax/libs/slick-carousel/1.6.0/slick-theme.min.css");
@import url("https://fonts.googleapis.com/css?family=Karla:400,700|PT+Sans:400,700|Source+Sans+Pro:400,700,900");
.characters {
  box-sizing: border-box;
  padding: 0 10px;
  outline: none; }

html {
  background: #f5f5f5;
  font-family: 'PT Sans';
  text-rendering: optimizeLegibility;
  box-sizing: border-box; }

body {
  margin: 0; }

header {
  background: #232f34;
  color: white;
  padding: 20px;
  margin-bottom: 60px; }
  @media (max-width: 960px) {
    header {
      padding-left: 0; } }
  header a {
    color: white;
    text-decoration: none; }

.main-container {
  max-width: 960px;
  margin: 0 auto;
  padding: 0;
  position: relative;
  overflow: hidden; }
  @media (max-width: 960px) {
    .main-container {
      width: 94%;
      margin: 0 auto;
      padding: 0;
      box-sizing: border-box; } }
  @media (min-width: 1540px) {
    .main-container {
      max-width: 1200px; } }

.slick-initialized .slick-slide {
  width: fit-content; }

.slick-list {
  width: 88%;
  margin: 0 auto; }

.slick-next, .slick-prev {
  width: 30px;
  height: 30px; }

.slick-next:before, .slick-prev:before {
  font-size: 30px;
  color: #666; }

img {
  height: 100%;
  display: block;
  position: relative; }

img:before {
  content: " ";
  display: block;
  position: absolute;
  left: 0;
  height: 176px;
  width: 98%;
  background-color: #ebebeb;
  border: 2px dotted #c8c8c8; }

img:after {
  content: "ðŸ”—" " Broken Image of " attr(alt);
  font-size: 16px;
  font-style: normal;
  color: #646464;
  position: absolute;
  width: 100%;
  text-align: center;
  display: flex;
  justify-content: center;
  height: 100%;
  align-items: center;
  top: 0; }

p {
  font-family: 'Karla';
  font-size: 18px;
  margin: 0; }

.slots > p {
  color: #646464; }

.arrow {
  width: 24px;
  height: 32px;
  position: absolute;
  bottom: 2%;
  right: 1%; }
  .arrow svg {
    fill: #646464; }

.bounce {
  animation: bounce 2s infinite; }

@keyframes bounce {
  0%, 20%, 50%, 80%, 100% {
    transform: translateY(0); }
  40% {
    transform: translateY(-30px); }
  60% {
    transform: translateY(-15px); } }
